@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
:root {
  --primary-brend: #9bb537;
  --primary-bg-color: #f8f8f8;
  --primary-cards-overlay: linear-gradient(1deg, #05050599 0%, #05050500 100%);
  --primary-title-color: #050505;
  --primary-text-color: #050505cc;
  --primary-input-title-color: #05050580;
  --primary-input-text-color: #0505054d;
  --primary-input-border: #05050533;
  --primary-scroll-bar: #0505052e;
  --rating-stars-active: #eea10c;
  --rating-stars-unactive: #d9d9d9;
  --rating-stars-unactive-cards: #f8f8f81a;
  --rating-stars-unactive-modal: #0505051a;
  --unactive-btn-arrow: #f8f8f880;
  --primary-switch-btn: #cecdcd;
  --dark-bg-color: #161616;
  --dark-modal-bg: #050505;
  --dark-cards-overlay: linear-gradient(1deg, #050505 0%, #05050500 100%);
  --dark-title-color: #fff;
  --dark-text-color: #fffc;
  --dark-input-title-color: #ffffff80;
  --dark-input-text-color: #ffffff4d;
  --dark-input-border: #fff3;
  --dark-scroll-bar: #ffffff2e;
  --dark-rating-stars-unactive: #3c3b3b;
  --dark-rating-stars-unactive-cards: #ffffff1a;
  --dark-switch-btn: linear-gradient(91deg, #9bb537 0%, #9bb537b0 100%);
}

*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  color: var(--primary-title-color);
  background-color: var(--primary-bg-color);
  min-height: 100vh;
  font-family: Inter;
}

ul[class], ol[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.container {
  justify-content: center;
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

@media screen and (width >= 375px) {
  .container {
    min-width: 375px;
    max-width: none;
  }
}

@media screen and (width >= 768px) {
  .container {
    min-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    min-width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

.header {
  padding-top: 24px;
}

.header-container {
  display: none;
}

.header-nav {
  gap: 16px;
  display: flex;
}

.mob-container {
  justify-content: space-between;
  display: flex;
}

.header-nav-link {
  color: var(--primary-title-color);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.is-active-link {
  color: var(--primary-brend);
}

.header-logo {
  align-self: center;
}

.header-logo-link {
  color: var(--primary-title-color);
  letter-spacing: -.64px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.12;
}

.header-span {
  color: var(--primary-brend);
}

.header-buttons {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header-cart-btn {
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
}

.header-cart {
  width: 24px;
  height: 24px;
}

.header-burger-btn {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
}

.header-burger {
  width: 32px;
  height: 32px;
}

.header-burger:hover, .header-burger:focus, .header-cart:hover, .header-cart:focus {
  fill: var(--primary-brend);
}

.header-switch {
  width: 46px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switch .header-input {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.header-slider:before {
  content: "";
  background-color: #fff;
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
}

.header-input:checked + .header-slider {
  background-color: var(--primary-brend);
}

.header-input:focus + .header-slider {
  box-shadow: 0 0 1px var(--primary-brend);
}

.header-input:checked + .header-slider:before {
  transform: translateX(26px);
}

.header-slider.round {
  border-radius: 34px;
}

.header-slider.round:before {
  border-radius: 50%;
}

@media screen and (width >= 768px) {
  .header {
    padding-top: 30px;
  }

  .mob-container {
    display: none;
  }

  .header-container {
    justify-content: space-between;
    display: flex;
  }

  .header-nav-container {
    align-self: center;
  }

  .header-buttons {
    gap: 18px;
  }
}

@media screen and (width >= 1200px) {
  .header {
    padding-top: 40px;
    padding-bottom: 93px;
  }

  .header-buttons {
    gap: 24px;
  }
}

.mobile-menu {
  background-color: var(--primary-brend);
  z-index: 11;
  opacity: 0;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 100%;
  transition: transform .3s, opacity .3s;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  overflow-y: hidden;
  transform: translateX(100%);
}

.visible {
  opacity: 1;
}

--------------------------- .dark-mode .mobile-menu {
  background-color: var(--dark-mode-bg);
}

.close-menu-icon {
  fill: var(--dark-title-color);
  width: 32px;
  height: 32px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu-nav {
  flex-direction: column;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
}

.mobile-menu-nav-links {
  color: #fff;
  margin-bottom: 16px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.mobile-menu-nav-links:last-child {
  margin-bottom: 0;
}

.mobile-menu-nav-links.active-link {
  font-size: 500px;
}

.mobile-menu-nav-links:hover {
  color: #1e1e1e;
}

.dark-mode .mobile-menu-nav-links:hover, .mobile-menu-nav-links.dark-mode-hover:hover {
  color: var(--primary-brend);
}

.mobile-menu-switcher {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.no-scroll {
  height: 100vh;
  overflow: hidden;
}

@media screen and (width >= 768px) {
  .block-for-mobile, .mobile-menu {
    display: none;
  }

  .tablet-desktop {
    display: block;
  }

  .tablet-desktop-nav {
    justify-content: space-between;
    align-items: center;
    padding-top: 28px;
    display: flex;
  }

  .navigations {
    margin-top: 31px;
  }

  .tablet-menu-nav-links {
    font-size: 12px;
    font-weight: 500;
    line-height: normal;
  }

  .tablet-menu-nav-links:not(:last-child) {
    margin-right: 16px;
  }

  .tablet-menu-nav-links:hover {
    color: var(--primary-brend);
    transition: all .3s;
  }

  .tablet-desktop-nav-icons {
    align-items: center;
    margin-top: 20px;
    display: flex;
  }

  .switcher {
    flex-shrink: 0;
    margin-left: 16px;
    margin-right: -22px;
  }

  .logo-first-color, .logo-second-color {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 20px;
  }
}

@media screen and (width >= 1280px) {
  .header {
    padding-bottom: 34px;
  }

  .navigations {
    margin-top: 43px;
  }

  .logo-block {
    margin-top: 40px;
  }

  .tablet-desktop-nav-icons {
    margin-top: 32px;
  }
}

.hero-container {
  flex-direction: column;
  padding-top: 92px;
  padding-bottom: 42px;
  display: flex;
}

.hero-block {
  margin-left: auto;
  margin-right: auto;
}

.hero-title {
  text-transform: uppercase;
  margin-bottom: 14px;
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.31;
}

.hero-span {
  color: var(--primary-brend);
  font-style: italic;
  font-weight: 400;
}

.hero-descr {
  color: var (--primary-title-color);
  margin-bottom: 24px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
}

.hero-btn {
  color: var(--primary-title-color);
  border: 1px solid #9bb537;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 48px;
  padding: 10px 20px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: inline-flex;
}

.hero-text {
  width: 335px;
  margin-bottom: 28px;
}

.swiper-container {
  gap: 8px;
  display: flex;
}

.images {
  width: 100%;
  display: flex;
}

.image {
  background-color: var(--primary-title-color);
  height: 280px;
}

.image:not(:last-child) {
  border-radius: 10px;
}

.cook > img {
  object-fit: cover;
  border-radius: 10px;
  width: 80px;
  height: 280px;
}

.dish {
  width: 200px;
  position: relative;
}

.dish > img {
  background: radial-gradient(circle, #9bb53780 0%, #0505054d 75%);
  width: 150px;
  height: 150px;
  margin-bottom: 70px;
  translate: 15% 35%;
}

.dish-zoom {
  border-radius: 10px 0 0 10px;
  overflow: clip;
}

.dish-zoom > img {
  object-position: -125px -80px;
  object-fit: cover;
  width: 60px;
  height: 442px;
}

.dish-descr {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.dish-name {
  color: #f8f8f899;
  text-align: center;
  text-transform: uppercase;
  max-width: 144px;
  margin: auto;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.dish-area {
  color: #f8f8f833;
  text-align: center;
  width: 24px;
  margin-top: 4px;
  margin-left: auto;
  margin-right: auto;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  display: flex;
}

.swiper {
  width: 335px;
  height: 320px;
}

.swiper-container {
  margin-left: auto;
}

.swiper-pagination-bullet {
  text-align: center;
  letter-spacing: 2px;
  background-color: gray;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.swiper-pagination-bullet-active {
  background-color: var(--accent-color);
  letter-spacing: 2px;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  width: 360px;
}

@media screen and (width >= 768px) {
  .hero-container {
    flex-direction: column;
    max-width: none;
    padding-bottom: 80px;
    padding-left: 32px;
  }

  .hero-block {
    margin-left: 0;
    margin-right: 0;
  }

  .hero-text {
    margin-bottom: 32px;
  }

  .hero-title {
    width: 446px;
    margin: 0 0 16px;
    font-size: 36px;
  }

  .hero-descr {
    width: 408px;
    font-size: 16px;
    line-height: 24px;
  }

  .hero-btn {
    margin-bottom: 64px;
    padding: 14px 28px;
  }

  .swiper {
    width: 736px;
    height: 490px;
  }

  .swiper-container {
    gap: 16px;
  }

  .cook > img {
    width: 137px;
    height: 442px;
  }

  .dish {
    width: 351px;
    height: 442px;
  }

  .dish > img {
    width: 262px;
    height: 262px;
    margin-bottom: 102px;
    translate: 15% 25%;
  }

  .dish-zoom {
    border-radius: 10px 0 0 10px;
    height: 442px;
    overflow: clip;
  }

  .dish-zoom > img {
    object-position: -80px -10px;
    object-fit: cover;
    width: 216px;
    transform: scale(1.3);
  }

  .dish-name {
    max-width: 276px;
    font-size: 14px;
    line-height: 18px;
  }

  .dish-area {
    margin-top: 8px;
    font-size: 12px;
    line-height: 14px;
  }

  .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    width: 660px;
  }
}

@media screen and (width >= 1200px) {
  .hero-container {
    flex-flow: row;
    justify-content: space-between;
    gap: 32px;
    max-width: none;
    padding-bottom: 100px;
    padding-left: 100px;
  }

  .hero-block {
    flex-direction: column;
    margin-top: 66px;
    display: flex;
  }

  .hero-text {
    width: 446px;
  }

  .swiper-container {
    gap: 16px;
    height: 442px;
    display: flex;
  }

  .cook > img {
    object-fit: cover;
    border-radius: 10px;
    width: 137px;
    height: 442px;
  }

  .dish {
    width: 351px;
    height: 442px;
    position: relative;
  }

  .dish > img {
    width: 262px;
    height: 262px;
    margin-bottom: 95px;
    translate: 45px 50px;
  }

  .dish-zoom {
    overflow: clip;
  }

  .dish-zoom > img {
    object-position: -100px -10px;
    object-fit: cover;
    width: 175px;
    height: 442px;
    transform: scale(1.3);
  }

  .dish-descr {
    text-align: center;
    flex-direction: column;
    display: flex;
  }

  .swiper {
    width: 702px;
    height: 480px;
  }
}

