@import './root.css';
@import './popular.css';

/* Placing of main */
.main-div {
    display: flex;
    flex-direction: row;
}

#side-bar {
    display: flex;
    flex-direction: column;
}

#content {
    display: flex;
    flex-direction: column;
}

/* START********************************************************************************* */
/*  */
.all-category-btn {
    width: 176px;
    height: 50px;
    padding: 14px 29px;
    border-radius: 15px;
    border: 1px, #050505;
    background-color: transparent;
    margin-bottom: 24px !important;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.21;
    text-wrap: nowrap;
}

.categories-box {
    margin-bottom: 40px;
}

.category-item-list:not(:last-child) {
    margin-bottom: 18px;
}

.category-btn {
    background-color: transparent;
    border-color: transparent;
}

.category-list {
    padding-left: 0px;
}

.categories-box {
    max-width: 326px;
    height: 198px;
    overflow-y: scroll;
}

.filter-item {
    color: #050505;
    border: 1px solid #05050533;
    border-radius: 15px;
    gap: 40px;
    padding: 14px 18px;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    display: inline-flex;
}

/*   */
.list-item {
    margin: 0 auto !important;
}

.item-cards:not(:last-child) {
    margin-bottom: 18px;
}

.card-img {
    max-width: 335px;
}

/*  */
.filters {
    display: flex;
    flex-wrap: wrap;
}

.filter-area {
    display: flex;
    flex-direction: column;
}

.filter-input {
    max-width: 335px;
}

.time {
    width: 159px;
}

.area {
    width: 160px;
}

.ingrediends {
    width: 188px;
}

.ingrediends-box {
    margin-bottom: 14px;
}

.label {
    margin-bottom: 8px;
    font-family: Inter;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.17;
}

.cards {
  margin-bottom: 40px;
}

/* ****************************************************************** */
.pagination {
  display: flex;
  margin: 0 auto;
}

.main-pag-btn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  margin-right: 8px;
  font-family: inter;
  border-color: #0505054D;
  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-pag-btn[disabled] {
  color: #ffffff33;
  background-color: #050505;
}

.main-pag-btn.main-pag-btn-green {
  border: 1px solid #9BB537;
  border-radius: 50%;
  background-color: #9BB537;
}

.main-pag-btn:hover:not([disabled]) {
  border-color: #ff00004d;
  border-radius: 50%;
  background-color: rgb(245, 130, 89);
  color: yellowgreen;
  width: 44px;
  height: 44px;
  margin-right: 8px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1),
    color .25s cubic-bezier(.4, 0, .2, 1),
    background-color .25s cubic-bezier(.4, 0, .2, 1),
    box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.main-pag-btn.active:hover {
  border-radius: 50%;
  background-color: red;
  color: rgb(0, 0, 0);
  width: 44px;
  height: 44px;
  margin-right: 8px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1),
    color .25s cubic-bezier(.4, 0, .2, 1),
    background-color .25s cubic-bezier(.4, 0, .2, 1),
    box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.main-pag-btn.active {
  border-radius: 50%;
  background-color: #9BB537;
  color: rgb(253, 253, 253);
  width: 44px;
  height: 44px;
  margin-right: 8px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1),
    color .25s cubic-bezier(.4, 0, .2, 1),
    background-color .25s cubic-bezier(.4, 0, .2, 1),
    box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

/* MIN 375**************************************************************** */
@media screen and (min-width: 375px) {
    #content {
        width: 335px;
    }

    .filter-input {
        width: 335px;
    }

    .list-item {
        margin: 0 auto !important;
        width: 335px;
    }
}

/* MIN 768******************************************************* */
@media screen and (min-width: 768px) {
    /*  */
    #content {
        width: 496px;
    }

    #categories {
        margin-right: 32px;
    }

    .all-category-btn {
        margin-bottom: 30px;
    }

    .categories-box {
        margin-bottom: 64px;
    }

    .category-item-list:not(:last-child) {
        margin-bottom: 24px;
    }

    /*   */
    .list-item {
        display: flex;
        flex-wrap: wrap;
        width: 496px;
        row-gap: 16px;
        column-gap: 16px;
    }

    .item-cards:not(:last-child) {
        margin-bottom: 0px;
    }

    .card-img {
        max-width: 240px;
    }

    /*  */
    .filter-input {
        width: 278px;
    }

    .time {
        width: 125px;
    }

    .area {
        width: 141px;
    }
}

.filters {
    gap: 16px;
}

.label {
    font-size: 14px;
    line-height: 1.29;
}

.cards {
  margin-bottom: 64px;
}

/* ******************************************************** */

/* MIN 1280******************************************************/
@media screen and (min-width: 1280px) {
    /*  */
    #content {
        width: 782px;
    }

    .all-category-btn {
        margin-bottom: 42px;
    }

    /*   */
    .card-img {
        max-width: 250px;
    }

    .list-item {
        row-gap: 32px;
        width: 782px;
    }
}
