@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}
ul {
    margin: 0;
    padding-left: 0;
}
img {
    display: block;
    max-width: 100%;
    height: 100%;
    width: 100%;
}

button {
    cursor: pointer;
}
.link {
    text-decoration: none;
    cursor: pointer;
}
.populars {
    width: 235px;
}
.popular-recepies-title {
    color: var(--primary-title-color);
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.16; /* 116.667% */
    text-transform: uppercase;
    margin-bottom: 32px;
}
.main-popular-recepies {
    width: 235px;
}
.popular-recepies-item {
    list-style: none;
}
.popular-recepies-item:not(:last-child) {
    margin-bottom: 32px;
}
.thomb-popular-wrap {
    display: flex;
    gap: 16px;
}

.thomb-popular-img {
    width: 64px;
    height: 64px;
    border-radius: 8px;
    overflow: hidden;
}
.thomb-popular-info {
    width: 155px;
}
.thomb-popular-title {
    color: var(--primary-title-color);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.28; /* 128.571% */
    text-transform: uppercase;
    text-overflow: ellipsis;
    white-space: normal;
    height: 15px;
    overflow: hidden;
    margin-bottom: 8px;
}
.popular-recepie-descr {
    height: 41px;
    overflow: hidden;
    color: var(--primary-text-color);
    text-overflow: ellipsis;
    white-space: normal;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px; /* 140% */
}
